* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

$primary-dark-color: #0e0e0e;
$secondary-dark-color: #151515;
$primary-light-color: #dadada;
$text-color: #393939;
$logo-dark-color: #0e196e;
$logo-light-color: #2a80f8;

@mixin nav2-row-style {
    height: 100%;
    text-align: center;
    padding: 7px 0px 14px 0px;
}

