@keyframes header-animate {
    0% {
        clip-path: polygon(0 0, 100% 0, 100% 70%, 25% 100%, 0 70%);
    }
    100% {
        clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
    }
}
