// #0e0e0e - górna nawigacja 1
// #151515 - górna nawigacja 2
// #2a80f8 - dolna belka w górnej navigacji 2
// #0e196e - nagłówki, przyciski "rozwiń", strzałki
// #dadada - teksty i ikony w nawigacji
// #393939 - główny tekst na stronie i przyciski pod nim

body {
    font-family: 'Muli', sans-serif;
    font-size: 13px;
}

.margin-top-style {
    margin-top: 4px;
}

.navbar-dark .navbar-nav .nav-link {
    color: $primary-light-color;

    i {
        font-size: 13px;
    }
}
.main-nav1 {
    background-color: $primary-dark-color;
    overflow-y: hidden;

    .nav-link-style {
        i {
            margin: 15px;
            font-size: 13px;
        }
    }
}

.dropdown-menu {
    min-width: 1rem;
    background-color: $primary-dark-color;
    color: $primary-light-color;
    font-size: 13px;
    font-family: 'Muli', sans-serif;
    padding-bottom: 0px;
    padding-top: 0px;
    border: 0;
}

.dropdown-menu li:hover {
    background-color: $secondary-dark-color;
}

.input-close-icon i {
    font-size: 20px;
    color: $primary-light-color;
}

.link-active {
  border-bottom: solid 2px $secondary-dark-color;
}

.navbar-expand-lg .navbar-nav .nav2-link-style {
    padding-right: 1.25rem !important;
    padding-left: 1.25rem !important;
    padding-bottom: 1rem;
    color: #dedede;
    border-bottom: solid 2px $secondary-dark-color;

    &:hover { 
        border-bottom: solid 2px $logo-light-color;
    }
}

img#logo-main-nav {
    width: 30px;
    margin-right: 15px;
    margin-top: 0;
}

.search-input {
    background-image: linear-gradient(
            to right bottom,
            rgba($secondary-dark-color, 1),
            rgba($secondary-dark-color, 1)
        ),
        url(../img/LUPA.png);
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
    border-radius: 0.8rem;
    width: 330px;
    height: 25px;
    margin-top: 7px;
    border: 1px solid $secondary-dark-color;
    color: $primary-light-color;
    padding-left: 10px;

    &:focus {
        outline: none;
    }
}

#nav2 {
    background-color: $secondary-dark-color;
    padding-bottom: 0;
    // height: 50px;

    .nav2-container-style {
        @include nav2-row-style;

        .nav2-row-style {
            @include nav2-row-style;

            .border-style {
                // height: 36px;

                &:hover {
                    border-bottom: solid 2px $logo-light-color;
                }
            }
        }
    }

    a {
        text-decoration: none;
        color: $primary-light-color;
    }
}

// header

#main-header {
    background-size: cover;
    background-position: center;
    height: 84.5vh;
    color: white;
}


.header-caption {
    padding-left: 100px;
    position: fixed;
    color: white;

    img {
        height: 130px;
        margin: 40px 0 20px 0;
    }

    .header-text-container {
        width: 50%;

        h1 {
            font-size: 3.5em;
            margin-bottom: 25px;
        }

        .header-icon {
            color: white;
            padding: 2px;
            font-size: 16px;
        }
    }
}

// section

.section-container {
    padding-top: 50px;
    width: 100%;
    overflow-x: hidden;
    overflow-y: hidden;

    .img-container {
        margin-top: 50px;
    }

    img {
        width: 350px;
        height: 350px;
        border-radius: 10%;
        box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.5);
    }

    .section-img-left {
        transform: rotate(15deg);
        margin-left: -50px;
    }

    .section-img-right {
        transform: rotate(-15deg);
        margin-right: -50px;
        margin-left: 80px;
    }

    .article-text {
        width: 60%;

        padding: 120px 200px 120px 200px;

        h4 {
            margin-bottom: 1.5rem;
            color: $logo-dark-color;
        }

        p {
            color: $text-color;
        }
    }
}

// footer

footer {
    img#footer-logo {
        max-width: 22%;
        margin-bottom: 2.5rem;
    }

    .footer-link {
        padding-right: 0.85rem !important;
        padding-left: 0.85rem !important;
        color: $text-color;

        &:hover,
        &:focus {
            color: $logo-light-color;
            text-decoration: none;
        }
    }

    .navbar-light .navbar-nav .footer-link-1 {
        color: $text-color;

        &:hover,
        &:focus {
            color: $logo-light-color;
            text-decoration: none;
        }
    }
}

div#copyright {
    height: 1.75rem;
    margin-top: 0.94rem;
    margin-bottom: 0.5rem;

    p {
        color: $primary-dark-color;
        padding: 0.2rem;
    }
}

#scroll-button {
    height: 40px;
    width: 40px;
    position: fixed;
    bottom: 25px;
    right: 25px;
    border: solid 1px $logo-light-color;
    border-radius: 100%;
    // background-color: #0e19;
    text-align: center;

    i {
        color: $logo-light-color;
        font-size: 12px;
    }
}

.team-container {
    padding: 103px 40px 0 40px;
    // padding-right: 40px;
    overflow-x: hidden;

    .team {
        padding-top: 50px;
        padding-bottom: 50px;
        font-weight: bold;
        margin: 0 auto;
        }

    .round-container {
        width: 100%;
        margin: 0 auto;

        .container-foto-team {
            width: 120px;
            height: 120px;
            border-radius: 100%;
            background-position: center;
            background-size: cover;
            margin: 0 auto;	
            padding-bottom: 30px;
        }

        .img-team1 {
            background-image: url(../img/lukas.jpg);
        }

    }

        .text-team-content {
            margin-bottom: 60px;

            .title-foto {
                font-weight: bold;
                font-size: 1em;
                text-align: center;
                padding-top: 20px;
            }
            
            .text-foto {
                font-size: 1em;
                text-align: center;
                margin-bottom: 5px;
            }

        }
}


@media all and (max-width: 992px) {
    .to-hide {
        display: none;
    }


    .search-input {
        width: 100%;
        border-radius: 0;
        padding: 12px;
        margin-top: 15px;
        height: 35px;
    }
    .navbar-collapse {
        margin-left: 20px;
        margin-right: 20px;
    }

    .navbar-dark .navbar-nav .nav-link {
        border-bottom: 1px solid $text-color;
        padding: 12px;
        margin: 0;

        &:hover {
            background-color: $secondary-dark-color;
        }
    }

    #nav2 {
        background-color: $primary-dark-color;
        margin-left: 20px;
        margin-right: 20px;
        padding: 0; 

        a {
        text-align: end;
        }
       
    }

    .team-container {
        padding-left: 0;
        padding-right: 0;
        overflow-x: hidden;

    }

    .section-container {
        .article-text {
            padding: 0 40px;
            margin-top: 30px;
            margin-right: 0;
            margin-left: 0;
            width: 100%;
        }
        .section-img-left {
            transform: rotate(0);
            margin-left: auto;
            margin-right: auto;
        }

        .section-img-right {
            transform: rotate(0);
            margin-right: auto;
            margin-left: auto;
        }
    }
}

@media all and (max-width: 425px) {
    .main-nav1 .nav-item {
        text-align: center;
    }

    #main-header .header-caption {
        padding-left: 70px;
        position: fixed;

        img {
            height: 100px;
            margin: 50px 0 20px 0;
        }

        .header-text-container {
            width: 70%;

            h1 {
                font-size: 2.5em;
                margin-bottom: 20px;
            }
        }
    }

    .section-container {
        .article-text {
            // padding: 10px 50px 10px 50px;
            margin-top: 40px;
            margin-right: 0;
            margin-left: 0;
            width: 100%;
        }
        .section-img-left {
            transform: rotate(0);
            margin-left: auto;
        }

        .section-img-right {
            transform: rotate(0);
            margin-right: auto;
            margin-left: auto;
        }
        .img-container {
            margin-top: 40px;
        }

        img {
            width: 75vw;
            height: 75vw;
        }

        .section-img-right {
            margin-left: 0px;
        }
    }

    .dropdown .dropdown-menu a.nav-link {
        text-align: center;
        padding: 8px 0;
        border-top: 1px solid rgba(119, 119, 119, 0.5);
    }
}
